html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  font-family: "Montserrat", sans-serif;
  font-size: 1.6rem;
}
body.fijar {
  overflow: hidden;
}
body.bodyScroll {
  padding-top: 8.5rem;
}

h1,
h2,
h3 {
  margin: 0 0 4rem 0;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
}

p {
  font-size: 2rem;
  color: #000;
}

a {
  text-decoration: none;
  line-height: 1.6;
}

ul {
  list-style: none;
}

img {
  display: block;
  max-width: 100%;
  width: 100%;
  height: auto;
}

.container {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

.header {
  background-color: #4cb8d3;
}
@media (min-width: 700px) {
  .header.fijo {
    top: 0;
    left: 0;
    position: fixed;
    width: 100%;
    box-shadow: 0.1rem 0.2rem 0.1rem #333;
  }
}
.header h1 {
  text-align: center;
  color: #fff;
}
@media (min-width: 700px) {
  .header h1 {
    margin: 0;
    font-size: 3.7rem;
  }
}

.content__header {
  padding: 2rem;
}
@media (min-width: 700px) {
  .content__header {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}

.nav a {
  display: block;
  text-align: center;
  font-size: 2.4rem;
  color: #fff;
  margin-bottom: 1rem;
  transition: 0.2s all ease-in-out;
}
@media (min-width: 700px) {
  .nav a {
    margin: 0;
  }
}
.nav a:hover {
  color: #fef46d;
}
@media (min-width: 700px) {
  .nav {
    display: flex;
    gap: 2rem;
  }
}

.video {
  position: relative;
  min-height: 50rem;
  max-height: 70rem;
  overflow: hidden;
}
.video .overlay {
  display: flex;
  flex-direction: column;
  justify-content: center;
  position: absolute;
  height: 100%;
  width: 100%;
  background: #090979;
  background: linear-gradient(90deg, rgba(9, 9, 121, 0.5) 8%, rgba(0, 212, 255, 0.2525209913) 92%);
}
.video video {
  width: auto;
  display: block;
}
@media (min-width: 900px) {
  .video video {
    width: 100%;
  }
}

.content__video h2 {
  font-size: 4rem;
  color: #fff;
  margin: 0;
}
@media (min-width: 700px) {
  .content__video h2 {
    font-size: 6rem;
  }
}
.content__video p {
  display: inline-block;
  background-color: #f53756;
  margin: 0;
  padding: 1rem 2rem;
  font-size: 2rem;
  font-weight: bold;
  color: #fff;
}

.festival {
  padding: 5rem;
}
@media (min-width: 700px) {
  .festival {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
  }
}

.content__festival {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-top: 5rem;
}
@media (min-width: 700px) {
  .content__festival {
    margin-top: 0;
  }
}
.content__festival h2 {
  margin-bottom: 2rem;
}
.content__festival .date {
  color: #f53756;
  font-weight: bold;
}

.lineup {
  background-color: #f53756;
  padding: 5rem 0;
}
.lineup h3,
.lineup p {
  text-align: center;
  color: #fff;
}
.lineup .day {
  text-transform: uppercase;
  margin: 5rem 0;
}

@media (min-width: 700px) {
  .content__lineup {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
  }
}
.content__lineup p {
  color: #000;
}

.escenario {
  padding-top: 2rem;
}
.escenario .name__escenario::after {
  content: "";
  display: block;
  margin: 2rem auto 0 auto;
  width: 20rem;
  height: 10rem;
  background-size: 15rem;
  background-repeat: no-repeat;
  background-position: center center;
}

.rock .name__escenario::after {
  background-image: url(../assets/img/icons/icono_rock.png);
}

.edm .name__escenario::after {
  background-image: url(../assets/img/icons/icono_edm.png);
}

.bg-amarillo {
  background-color: #fef46d;
}
.bg-amarillo li:nth-child(odd) {
  background-color: #b7ab01;
  color: #fff;
}

.bg-verde {
  background-color: #4cb8d3;
}
.bg-verde li:nth-child(odd) {
  background-color: #1a5b6c;
  color: #fff;
}

.calendar {
  margin: 0;
  padding: 0;
  list-style: none;
}
.calendar li {
  padding: 2.5rem;
  font-weight: 700;
}
.calendar li span {
  font-weight: 400;
}

.gallery {
  padding: 5rem 0;
}
.gallery h3 {
  text-align: center;
}

.gallery__img {
  margin: 0;
  padding: 0;
  list-style: none;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 2rem;
}
@media (min-width: 480px) {
  .gallery__img {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }
}
@media (min-width: 700px) {
  .gallery__img {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 2rem;
  }
}
@media (min-width: 900px) {
  .gallery__img {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    gap: 2rem;
  }
}

.overlay {
  display: grid;
  place-items: center;
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  transition: 0.2s ease-in-out;
}
.overlay picture {
  display: block;
  max-width: 80rem;
  transition: 0.2s ease-in-out;
}

.btnClose {
  display: grid;
  place-items: center;
  height: 4rem;
  width: 4rem;
  font-weight: bold;
  color: #000;
  border-radius: 50%;
  background-color: #fff;
}
.btnClose:hover {
  cursor: pointer;
}

.boletos {
  padding: 5rem 0;
}
.boletos h3 {
  text-align: center;
  color: #fff;
  margin-bottom: 2rem;
}
@media (min-width: 700px) {
  .boletos .boletos__container {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
  }
}

.pase .pase__name {
  color: #fff;
  font-size: 2.4rem;
  text-align: center;
}
.pase__header p, .pase__footer p {
  text-align: center;
  color: #fff;
  padding: 0.5rem;
  margin: 0;
}
.pase__header {
  background-color: #f53756;
}
.pase__header p {
  text-transform: uppercase;
  font-size: 1.6rem;
}
.pase__body {
  background-color: #fff;
}
.pase__body ul {
  margin: 0;
  padding: 0;
  list-style: none;
}
.pase__body ul li,
.pase__body ul :nth-child(odd) {
  color: #000;
  background-color: #fff;
  padding: 1rem;
  border-bottom: 1px solid #e1e1e1;
}
.pase__footer {
  background-color: #752f97;
}
.pase__footer p {
  font-size: 2.4rem;
  font-weight: bold;
}

.footer {
  background-color: #f53756;
  padding: 1rem;
}
.footer p {
  margin: 0;
  text-align: center;
  color: #fff;
  font-weight: bold;
}