@use "variables" as v;

html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  font-family: v.$fuentePrincipal;
  font-size: 1.6rem;
  &.fijar {
    overflow: hidden;
  }
  &.bodyScroll {
    padding-top: 8.5rem;
  }
}

h1,
h2,
h3 {
  margin: 0 0 4rem 0;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}
h3 {
  font-size: 3rem;
}
p {
  font-size: 2rem;
  color: v.$negro;
}

a {
  text-decoration: none;
  line-height: 1.6;
}

ul {
  list-style: none;
}

img {
  display: block;
  max-width: 100%;
  width: 100%;
  height: auto;
}

.container {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}
